.footer{
  background-color: #1D1D1D;
  padding: 170px 180px 20px;
  // @media screen and (max-width: 1536px) {
  //   padding: 6rem;
  // } 
  @media screen and (max-width: 1536px) {
  padding: 100px 150px 20px;
  } 
  @media screen and (max-width: 1440px) {
  padding: 100px 120px 20px;
  } 
  @media screen and (max-width: 1024px) {
  padding: 50px 100px 20px;
  } 
  @media screen and (max-width: 768px) {
    padding: 4rem 3rem 20px ;
  } 
  @media screen and (max-width: 430px) {
    padding: 50px 20px 20px;
  }
}
.footer-logo {
  width: 264px;
  height: auto;

  @media screen and (max-width: 1440px) {
    width: 200px;
  } 
  @media screen and (max-width: 1280px) {
    width: 180px;
  } 
  @media screen and (max-width: 430px) {
    width: 150px;
  }
}

.footer-gap{
  gap: 128px;
  @media screen and (max-width: 1536px) {
    gap: 90px;
  }
  @media screen and (max-width: 1200px) {
    gap: 64px;
  }
  @media screen and (max-width: 768px) {
    gap: 25px;
  }
  @media screen and (max-width: 420px) {
    gap: 24px;
  }
}

ul{
  gap: 10px;
  @media screen and (max-width: 768px) {
    gap: 5px;
  }
}

.margin10{
  margin-bottom: 10px;
}

.marginTop128{
  margin-top: 128px;
  @media screen and (max-width: 1440px) {
    margin-top: 60px;
  }
  @media screen and (max-width: 820px) {
    margin-top: 30px;
  }
}


.footer-social-icon {
  width: 17px;
  height: 17px;
  object-fit: contain;
}

.youtube{
  filter: brightness(0) invert(1);
}
.social-icon-div{
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pad-left{
  @media screen and (max-width: 1920px) {
    padding-left: 107px;
  }
}

.back-to-top.active {
    visibility: visible;
    opacity: 1;
}
.back-to-top {
    background: transparent;
    width: 50px;
}
.back-to-top {
    position: fixed;
    visibility: hidden;
    opacity: 0;
    right: 15px;
    bottom: 15px;
    z-index: 996;
    // background: #e03a3c;
    width: 40px;
    height: 40px;
    border-radius: 4px;
    transition: all 0.4s;
}
.supportlinks{
  li{
    width: 144px;
    @media screen and (max-width: 1536px) {
      width: 126px;
    }
    @media(max-width:991px){
      width: 100%;
    }
  }
}


a:hover{
  font-family: 'RedHat Bold';
  transition: all 0.4s ease;
}